
.SignInBox {
  width: 100%;
}
.SignInBox .row div {
    background-color: #233;
}
.SignInBox .row input {
    width: 100%;
    height: 3em;
    margin-bottom: .3em;
    border-radius: 3px;
    border: 0;
}
.SignInBox .actions input {
    width: 100%;
    height: 3em;
    border-radius: 3px;
    margin-top: .2em;
    font-size: 16px;
}



